  width: 100%;
  height: 100%;
}
#vibrant div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: #000;
}
#post0 {
  width: 28%;
  position: absolute;
  top: 42%;
  left: 10%;
}
#post0 p:first-child {
  color: #fff;
  font-size: 12px;
}
#post0 h2 {
  font-size: 28px;
