}
.post:nth-child(odd) .else {
  left: 660px;
}
.post:nth-child(even) .else {
  right: 660px;
}
.else p:first-child {
  color: #999;
  font-size: 12px;
  margin: 80px 0 0 80px;
}
.else h3 {
  font-size: 30px;
