.comment.link, .downloadlink {
  cursor: pointer;
  letter-spacing: 5px;
  border: 1px solid #dadada;
  width: 170px;
  text-align: center;
  color: #666;
  font-size: 14px;
  padding: 10px 0;
}
.images .downloadlink {
  position: static;
  display: none;
  margin: 50px auto 70px;
}
.relate {
  background: #fafafa;
  padding: 50px 0;
}
.relate em {
  font-style: normal;
}
.relate ul {
  width: 700px;
  margin: 0 auto;
}
.relate h3 {
  font-size: 13px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  color: #333;
  position: relative;
}
.relate h3 span {
  position: absolute;
  right: 0;
  bottom: 10px;
}
.relate h3 a {
  color: #666;
  font-size: 14px;
  margin-left: 14px;
  display: inline-block;
}
.relate h3 a:hover {
  color: #333;
}
.relate li {
  padding: 25px 0 32px;
  border-bottom: 1px solid #e8e8e8;
}
.relate li div {
  display: inline-block;
  width: 70%;
  vertical-align: top;
  margin-right: 3%;
}
.relate li > a {
  display: inline-block;
  width: 27%;
  vertical-align: top;
}
.relate li div a {
  font-size: 24px;
  color: #333;
  line-height: 1.3;
}
.relate li div p {
  font-size: 14px;
  color: #666;
  line-height: 1.8;
  margin-top: 10px;
}
#preview {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background: #fff;
  left: 100%;
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.5, 0.5, 0.9);
  transition: all 0.3s cubic-bezier(0.25, 0.5, 0.5, 0.9);
  overflow-y: scroll;
}
#preview.trans {
  left: auto;
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
  -moz-transform: translateX(100%);
}
#preview.show {
  left: 0;
}
#preview.trans.show {
  left: auto;
  transform: translateX(0);
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
}
/* article content */
.content {
  font-size: 14px;
  line-height: 2;
  color: #333;
}
.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
  font-weight: normal;
  color: #333;
  margin: 10px 0 0;
}
.content h6 {
  font-size: 16px;
}
.content h5 {
  font-size: 18px;
}
.content h4 {
  font-size: 20px;
}
.content h3 {
  font-size: 22px;
}
.content h2 {
  font-size: 24px;
}
.content h1 {
  font-size: 26px;
}
.content blockquote {
  margin: 14px 0;
  padding-left: 40px;
  border-left: 3px solid #999;
}
.content a {
  color: #333;
  text-decoration: underline;
}
.content hr {
  height: 2px;
  width: 90%;
  margin: 30px 0 30px 5%;
  background: #eee;
  border: none;
}
.content ul, .content ol {
  margin: 10px 0;
  padding-left: 20px;
}
.content ul li {
  list-style: disc;
}
.content ol li {
  list-style: decimal;
}
.alignleft {
  float: left;
}
.alignright {
  float: right;
}
.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.friend {
  margin-top: 30px;
}
.friend li {
  display: inline-block;
  margin: 10px 10px 10px 0;
}
.friend a {
  display: block;
  color: #666;
  padding: 5px 13px;
  border: 1px solid #ddd;
  border-radius: 3px;
}
.friend a:hover {
  background: #f9f9f9;
}
#preview + canvas {
  display: none;
}
#reply-title {
  font-size: 18px;
  color: #333;
  margin-bottom: 16px;
  margin-top: 30px;
}
#reply-title a {
  margin: 0 5px;
  color: #333;
  text-decoration: underline;
}
.comment-notes {
  display: none;
}
#commentform input, #commentform textarea {
  display: block;
  vertical-align: top;
  border: 1px solid #e2e2e2;
  padding: 10px;
  width: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  font-size: 14px;
}
#commentform label {
  display: block;
  font-size: 14px;
  color: #444;
  margin: 10px 0 5px;
}
#commentform input[type=submit] {
  cursor: pointer;
  letter-spacing: 5px;
  border: 1px solid #dadada;
  width: 140px;
  text-align: center;
  color: #666;
  font-size: 14px;
  padding: 10px 0;
  background: #fff;
  margin: 30px auto 0;
}
#comments {
  margin-top: 70px;
}
#comments > h2 {
  color: #333;
  font-size: 18px;
}
#comments ol {
  padding-left: 0;
}
#comments li {
  position: relative;
  margin-top: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}
.says {
  display: none;
}
.comment-author {
  display: inline-block;
}
.comment-author img {
  display: inline-block;
  border-radius: 50%;
  vertical-align: top;
  margin-right: 10px;
}
.comment-author a {
  color: inherit;
}
.comment-author cite {
  color: #EF6D57;
  font-style: normal;
  font-size: 14px;
}
.comment-meta {
  margin: -14px 0 0 54px;
  font-size: 12px;
}
.comment-meta + p {
  margin-top: 14px;
  font-size: 14px;
  color: #333;
  word-break: break-all;
}
.comment-meta a {
  color: #666;
}
.reply {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 14px;
}
.reply a {
  color: #EF6D57;
  text-decoration: underline;
}
ol.children {
  margin-left: 30px;
}
li.depth-2 .children {
  margin-left: 0;
}
ol.children li {
  border-bottom: none!important;
  padding-bottom: 10px!important;
}
.logged-in-as a, .must-log-in a, .comment-meta + p a {
  color: #EF6D57;
}
.comment-navigation h2 {
  display: none;
}
.nav-links {
  margin-top: 20px;
  overflow: hidden;
}
.nav-links div {
  float: left;
  width: 50%;
}
.nav-links .nav-next {
  float: right;
  text-align: right;
}
.nav-links a {
  color: #EF6D57;
}
@media screen and (max-width: 1200px) {
  #header > div {
      width: 900px;
      margin: 0 auto;
  }
  #post0 {
      width: 32%;
  }
  #post0 h2 {
      font-size: 24px;
  }
  .post {
      width: 900px;
  }
  .post > a {
      width: 480px;
      height: 310px;
  }
  .else {
      top: 10px;
      width: 420px;
      height: 290px;
  }
  .post:nth-child(odd) .else {
      left: 450px;
  }
  .post:nth-child(even) .else {
      right: 450px;
  }
  .else p:first-child {
      font-size: 12px;
      margin: 50px 0 0 60px;
  }
  .else h3 {
      font-size: 24px;
      margin: 10px 60px 0 60px;
  }
  .else h3 + p {
      margin: 10px 60px 0 60px;
  }
  .here {
      left: 60px;
      bottom: 50px;
  }
}
@media screen and (max-width: 900px) {
  #mark {
      height: 480px;
  }
  #header > div {
      width: 90%;
  }
  #vibrant polygon {
      transform: translateX(-10%);
      -moz-transform: translateX(-10%);
      -webkit-transform: translateX(-10%);
  }
  #post0 {
      width: 40%;
  }
  #post0 h2 {
      font-size: 20px;
  }
  .post {
      width: 100%;
      margin-top: 60px;
      background: #fff;
      padding-bottom: 30px;
      border-bottom: 1px solid #eaeaea;
  }
  .post > a {
      display: block;
      margin: 0 auto;
      width: 100%;
      height: auto;
  }
  .post > a img {
      margin: 0 auto;
  }
  .else {
      height: auto;
      position: static;
      width: 96%;
      margin: 0 auto;
      background: transparent;
      border: none;
  }
  .else p:first-child {
      margin: 30px 0 0 20px;
  }
  .else h3 {
      font-size: 24px;
      margin: 10px 30px 0 20px;
  }
  .else h3 + p {
      margin: 10px 30px 0 20px;
  }
  .here {
      position: static;
      margin: 20px 0 20px 20px;
  }
}
@media screen and (max-width: 780px) {
  #header {
      top: 40px;
  }
  #vibrant svg {
      display: none;
  }
  #vibrant div {
      opacity: 0.3;
  }
  #container {
      padding-bottom: 50px;
  }
  #post0 {
      bottom: 60px;
      top: auto;
      width: 70%;
      left: 5%;
  }
  .menu {
      width: 80%;
      margin: 160px auto 0;
  }
  .menu li {
      font-size: 16px;
  }
  #pager {
      margin: 30px 0 0;
  }

  .article > div {
      width: 90%;
      padding: 30px 0;
  }
  h1.title {
      font-size: 20px;
  }
  .tab {
      display: none;
  }
  .icon-wechat {
      display: none!important;
  }
  .icon-images {
      left: 80px;
  }
  .icon-pause, .icon-play {
      left: 50px;
  }
  .relate ul {
      width: 90%;
  }
  .relate li div a {
      font-size: 18px;
  }
  .comment.link, #commentform input[type=submit] {
      width: 140px;
      padding: 5px 0;
  }
}
@media screen and (max-width: 480px) {
  #post0 p:first-child {
      display: none;
  }
  #post0 h2 {
      text-align: left;
      margin: 0 10px 0 0;
  }
  #post0 h2 + p {
      text-align: left;
      margin: 10px 20px 0 0;
      color: #f2f2f2;
      font-size: 12px;
  }
  #post0 h2 a {
      color: #fff;
  }
  .relate li {
      display: none;
  }
  .relate h3 > em {
      display: none;
  }
  .stuff span {
      margin-right: 10px;
  }
  .stuff span:first-child {
      display: none;
  }
}
@font-face {
  font-family: 'icomoon';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
